import {v4 as uuid} from 'uuid';


//ADD EXPENSE
export const addExpense=({ 
    description ='',
    note='',
    amount=0,
    createdAt=0
}={}
)=>({

    type:'ADD_EXPENSE',
    expenses:{
        id: uuid(),
        description,
        note,
        amount,
        createdAt,
    }

});

//REMOVE EXPENSE
export const RemoveExpense =({ id }={})=>({
    type: 'REMOVE_EXPENSE',
    id
});


//EDIT EXPENSE
export const editExpense =(id, updates)=>({
    type:'EDIT_EXPENSE',
    id,
    updates
});




